@import url('https://fonts.googleapis.com/css?family=Merienda');
@import url('https://fonts.googleapis.com/css?family=Merienda+One');

#jumb {
    margin-bottom: 0px;
}

#setHead {
    background: none;
    font-family: 'Merienda One', cursive;
    color: black;
    text-transform: uppercase;
    text-shadow: 2px 3px rgba(0, 0, 0, 0.42);
    font-size: 32px;
    font-weight: 900;
    margin: 1em 0 1em;
    line-height: 2em;
}

#setfooter {
    text-align: center;
    color: black;
    font-weight: 900;
    font-size: 1.2em;
    font-family: 'Merienda One', cursive;
    margin: 2em 0 2em;
    line-height: 2em;
}

.div nav {
    font-family: 'Merienda One', cursive;
    padding: 12px;
    font-size: 1em;
}

.navpad ul li {
    padding-right: 10px;
}

.activeLink {
    color: whitesmoke;
}

@media (min-width: 1500px) {
    .container {
        width: 1400px;
    }
}

.row {
    margin-left: -7.5px;
    margin-right: -7.5px;
}

.col-lg-1,
.col-lg-10,
.col-lg-11,
.col-lg-12,
.col-lg-2,
.col-lg-3,
.col-lg-4,
.col-lg-5,
.col-lg-6,
.col-lg-7,
.col-lg-8,
.col-lg-9,
.col-md-1,
.col-md-10,
.col-md-11,
.col-md-12,
.col-md-2,
.col-md-3,
.col-md-4,
.col-md-5,
.col-md-6,
.col-md-7,
.col-md-8,
.col-md-9,
.col-sm-1,
.col-sm-10,
.col-sm-11,
.col-sm-12,
.col-sm-2,
.col-sm-3,
.col-sm-4,
.col-sm-5,
.col-sm-6,
.col-sm-7,
.col-sm-8,
.col-sm-9,
.col-xs-1,
.col-xs-10,
.col-xs-11,
.col-xs-12,
.col-xs-2,
.col-xs-3,
.col-xs-4,
.col-xs-5,
.col-xs-6,
.col-xs-7,
.col-xs-8,
.col-xs-9 {
    padding-top: 7.5px;
    padding-bottom: 7.5px;
}

.card {
    background: #ffffff;
    margin: 7.5px 0;
    padding: 20px;
    border: 1px solid #e7e7e7;
    border-radius: 3px;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}
