.w3ls-login form {
	position: relative;
	max-width: 500px;
	margin: 0 5vw;
	padding: 3.5vw;
	border: none;
	background: -webkit-linear-gradient(47deg, #ffffff 44%, #00ba9d 42%);
	background: linear-gradient(43deg, #ffffff 44%, #00ba9d 42%);
	background: -webkit-linear-gradient(43deg, #ffffff 44%, #00ba9d 42%);
	background: -o-linear-gradient(43deg, #ffffff 44%, #00ba9d 42%);
	box-sizing: border-box;
	display: flex;
	border-top: 8px solid #fff;
	display: -webkit-flex;
	flex-wrap: wrap;
}

.w3ls-login label {
	font-size: 15px;
	color: #000;
	float: left;
	font-weight: 600;
	margin-bottom: 10px;
	text-transform: uppercase;
	letter-spacing: 1px;
	cursor: pointer;
	font-family: 'Merienda One', cursive;
}

.agile-field-txt {
	flex-basis: 100%;
	-webkit-flex-basis: 100%;
	margin-bottom: 1.5em;
	clear: both;
}

.w3ls-login label i {
	font-size: 1.1em;
	margin-right: 3px;
	color: #363af4;
}

.w3ls-login input[type="text"],
.w3ls-login input[type="password"] {
	width: 100%;
	color: #000;
	outline: none;
	font-size: 16px;
	letter-spacing: 0.5px;
	/* line-height: 25px; */
	padding: 15px;
	box-sizing: border-box;
	border: none;
	box-shadow: 3px 0px 12px rgba(0, 0, 0, 0.49);
	-webkit-appearance: none;
	font-family: 'Merienda One', cursive;
	background: #fff;
}

.check1 {
	text-align: left;
}

label.check {
	float: none;
	;
}

