}

.w3ls-login.w3l-sub {
	margin-top: 1em;
	flex-basis: 100%;
	-webkit-flex-basis: 100%;
}

.w3ls-login input[type=submit] {
	color: #00ba9d;
	font-weight: 600;
	width: 100%;
	padding: 0.5em 0;
	margin-top: 2em;
	font-size: 1.1em;
	letter-spacing: 2px;
	cursor: pointer;
	border: none;
	border-radius: 8px;
	border: 5px double #00ba9d;
	outline: none;
	background: #000;
	font-family: 'Merienda One', cursive;
	transition: 0.5s all ease;
	-webkit-transition: 0.5s all ease;
	-moz-transition: 0.5s all ease;
	-o-transition: 0.5s all ease;
	-ms-transition: 0.5s all ease;
}

.w3ls-login input[type=submit]:hover {
	background: #ffffff;
	color: #000;
}

.w3ls-bot {
	margin-top: 1em;
	width: 100%;
}

.w3ls-login img {
	position: absolute;
	top: -10%;
	left: 40%;
	border: 7px solid #00ba9d;
	border-left-color: #fff;
	border-right-color: #fff;
	border-radius: 50%;
}

/* switch */

label.switch {
	position: relative;
	display: inline-block;
	height: 23px;
	padding-left: 3.8em;
	cursor: pointer;
	color: #000;
}

li:nth-child(2) a,
label.switch {
	text-transform: capitalize;
	font-size: 14px;
	font-weight: 600;
	letter-spacing: 1px;
}

.switch input {
	display: none;
}

.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 3px;
	width: 20%;
	background-color: #777;
	-webkit-transition: .4s;
	transition: .4s;
}

.slider:before {
	position: absolute;
	content: "";
	height: 10px;
	width: 10px;
	left: 4px;
	bottom: 5px;
	background-color: white;
	-webkit-transition: .4s;
	transition: .4s;
}

input:checked+.slider {
	background-color: #363af4;
}

input:focus+.slider {
	box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
	-webkit-transform: translateX(18px);
	transform: translateX(18px);
}

/* Rounded sliders */

.slider.round:before {
	border-radius: 50%;
}

/* //switch */

.form-end {
	float: right;
	width: 35%;
}

