#setUpdateBack {
	background:url(/static/media/banner.6eb6e028.jpg) no-repeat 0px 0px;
	background-size:cover;
}

#setProd {
	font-family: 'Merienda One', cursive;
	text-transform: capitalize;
	font-weight: 500;
	text-align: center;
}

#setBtnLabel {
	font-family: 'Merienda One', cursive;
	text-transform: uppercase;
	font-weight: 900;
}

#set_fonty {
	font-family: 'Merienda One', cursive;
	text-transform: capitalize;
	font-weight: 900;
	text-align: center;
}

.top-img-agileits-w3layouts {
	background: url(/static/media/top.33dad3bd.jpg)no-repeat 0px 0px;
	background-size: cover;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	-ms-background-size: cover;
	border-top-right-radius: 18px;
	border-top-left-radius: 18px;
	padding: 30px 25px;
}

h2.sub-head-w3-agileits {
	text-align: left;
	font-size: 25px;
	text-transform: capitalize;
	letter-spacing: 3px;
	color: #fff;
	padding-bottom: 10px;
	border-bottom: 2px solid #b52e31;
	font-weight: 400;
}

.top-img-agileits-w3layouts p {
	color: #fffcfc;
	letter-spacing: 1px;
	text-align: left;
	font-size: 13px;
	/* line-height: 40px; */
	padding-top: 15px;
}

.login-form {
	padding: 2em;
}

.login-w3l {
	width: 22%;
	margin: 0 auto;
	background: rgb(0, 0, 0);
	border-radius: 18px;
	box-shadow: 10px 10px 16px 0px rgba(255, 255, 255, 0.1);
}

.login-form input[type="text"],
.login-form textarea,
.login-form input[type="email"] {
	width: 93%;
	padding: 10px;
	border: none;
	border-left: 1px solid #b52e31;
	outline: none;
	color: #fff;
	font-size: 13px;
	letter-spacing: 1px;
	margin-bottom: 15px;
	background: rgba(45, 45, 45, 0.35);
	transition: 0.5s all;
	-webkit-transition: 0.5s all;
	-o-transition: 0.5s all;
	-ms-transition: 0.5s all;
	-moz-transition: 0.5s all;
	font-family: 'Montserrat', sans-serif;
}

h1 {
	font-size: 35px;
	letter-spacing: 5px;
	color: #fff;
	margin: 2em 0 1.5em;
}

.login-form textarea {
	resize: none;
	height: 50px;
}

.login-form input[type="submit"] {
	background: #b52e31;
	color: #fff;
	font-size: 18px;
	border: none;
	text-transform: capitalize;
	border: 2px solid #b52e31;
	width: 100%;
	outline: none;
	cursor: pointer;
	-webkit-appearance: none;
	padding: 13px 0;
	letter-spacing: 1px;
	margin-top: 15px;
	transition: 0.5s all;
	-webkit-transition: 0.5s all;
	-moz-transition: 0.5s all;
	-o-transition: 0.5s all;
	font-family: 'Montserrat', sans-serif;
}

.login-form input[type="submit"]:hover {
	background: transparent;
}

.book-form input[type="text"],
.book-form input[type="email"],
.book-form input[type="password"] {
	width: 94%;
	padding: 10px 15px;
	border: none;
	border-bottom: 1px solid #7d7d7d;
	outline: none;
	font-size: 14px;
	letter-spacing: 1px;
	margin-bottom: 20px;
	background: transparent;
	transition: 0.5s all;
	-webkit-transition: 0.5s all;
	-o-transition: 0.5s all;
	-ms-transition: 0.5s all;
	-moz-transition: 0.5s all;
	color: #000 !important;
}

.footer-agileits p {
	text-align: center;
	color: #fff;
	font-size: 14px;
	margin: 3.5em 0;
	letter-spacing: 1.5px;
	display: block;
}

.footer-agileits p a:hover {
	color: #b52e31;
	text-decoration: none;
	transition: 0.5s all;
	-webkit-transition: 0.5s all;
	-o-transition: 0.5s all;
	-ms-transition: 0.5s all;
	-moz-transition: 0.5s all;
}

.footer-agileits p a {
	text-decoration: underline;
	color: #fff;
	transition: 0.5s all;
	-webkit-transition: 0.5s all;
	-o-transition: 0.5s all;
	-ms-transition: 0.5s all;
	-moz-transition: 0.5s all;
}

