@import url('https://fonts.googleapis.com/css?family=Merienda');

@import url('https://fonts.googleapis.com/css?family=Merienda+One');

.active {
    color: white;
}

#headings {
    text-align: center;
    color: black;
    text-transform: uppercase;
    font-weight: 900;
    font-size: 1.6em;
    font-family: 'Merienda One', cursive;
}

#setProductHeader {
    text-align: center;
    color: black;
    font-weight: 900;
    font-family: 'Merienda One', cursive;
}

#setCardBody {
    color: black;
    font-weight: 900;
    font-family: 'Merienda One', cursive;
}

#setfooter {
    text-align: center;
    color: black;
    font-weight: 900;
    font-size: 1.2em;
    font-family: 'Merienda One', cursive;
}

#setavatar {
    color: black;
    font-weight: 900;
    font-size: 15px;
    font-family: 'Merienda One', cursive;
}

#setSidebar {
    font-weight: 900;
    line-height: 2em;
    font-family: 'Merienda One', cursive;
}

#setList {
    font-weight: 900;
    line-height: 2em;
    font-family: 'Merienda', cursive;
}

#setMain span {
    font-family: 'Merienda One', cursive;
}

#setDivider {
    border-style: solid;
    border-width: 2px;
}

#setProdName {
    color: black;
    font-weight: 900;
    font-size: 18px;
    font-family: 'Merienda One', cursive;
    text-transform: capitalize;
}

#setProdBody {
    color: black;
    font-weight: 900;
    font-size: 16px;
    font-family: 'Merienda One', cursive;
    text-transform: capitalize;
}

#setProdLink {
    color: whitesmoke;
    font-weight: 900;
    font-size: 16px;
    font-family: 'Merienda One', cursive;
    text-transform: capitalize;
}


